




























































































import { Component, Vue } from "vue-property-decorator";
import { resetCanvas } from "../utils/boardSettings";
import { auth } from "../../firebase";
import Alert from "../customized/Alert.vue";

@Component({
  components: { Alert },
})
export default class TopBar extends Vue {
  active: string = "dark";
  title: string = "New";
  downloadModal: boolean = false;
  editTitle: boolean = false;
  logoutModal: boolean = false;
  tabNumber: number = 1;
  resetAlert: boolean = false;
  onColorChange(e: any) {
    this.resetAlert = false;
    this.tabNumber == 5
      ? (this.tabNumber = 1)
      : (this.tabNumber = this.tabNumber + 1);
    this.active = <string>this.getColor(this.tabNumber);
  }

  resetCanvas(): void {
    this.resetAlert = !this.resetAlert;
  }

  onCanvasReset(): void {
    this.resetAlert = false;
    resetCanvas();
  }

  onCanvasDownload(): void {
    this.downloadModal = true;
    const link = document.createElement("a");
    link.download = this.title + ".png";
    //@ts-ignore
    link.href = document.getElementById("c").toDataURL();
    link.click();
    setTimeout(() => {
      this.downloadModal = false;
    }, 5000);
  }
  onEditSubmit(): void {
    if (this.title) this.editTitle = false;
  }

  async onSignOut(): Promise<void> {
    this.logoutModal = false;
    await auth.signOut();
    this.$router.push("/");
  }

  getColor(n: number) {
    switch (n) {
      case 1:
        return "dark";
      case 2:
        return "success";
      case 3:
        return "danger";
      case 4:
        return "primary";
      case 5:
        return "warn";
    }
  }
}
